import React  from 'react'
import { push as Menu } from 'react-burger-menu'
import { Link } from "gatsby"

var styles = {
    bmBurgerButton: {
        position: 'absolute',
        width: '36px',
        height: '30px',
        right: '36px',
        top: '36px'
    },
    bmBurgerBars: {
        background: '#373a47'
    },
    bmBurgerBarsHover: {
        background: '#a90000'
    },
    bmCrossButton: {
        height: '24px',
        width: '24px'
    },
    bmCross: {
        background: '#bdc3c7'
    },
    bmMenuWrap: {
        position: 'fixed',
        height: '100%'
    },
    bmMenu: {
        background: '#373a47',
        padding: '2.5em 1.5em 0',
        fontSize: '1.15em'
    },
    bmMorphShape: {
        fill: '#373a47'
    },
    bmItemList: {
        color: '#b8b7ad',
        padding: '0.8em'
    },
    bmItem: {
        display: 'inline-block'
    },
    bmOverlay: {
        background: 'rgba(0, 0, 0, 0.3)'
    }
}

const linkColor = {
    color: '#fff'
}


var isMenuOpen = function(state) {
    return state.isOpen;
};

class Navigation extends React.Component {
    render () {
        return (
            <Menu right burgerButtonClassName={ "custom-burger-button" } pageWrapId={ "page-wrap" } outerContainerId={ "outer-container" } styles={ styles } onStateChange={ isMenuOpen }>
                <Link id="courses" className="menu-item" style={ linkColor } to="/courses">Courses</Link>
            </Menu>
        );
    }
}

export default Navigation
